body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
  background-color: #9bbbff;
  min-height: 100vh;
  font-family: 'Lato', sans-serif; }

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: black; }

a {
  text-decoration: none;
  color: yellow; }

.navbar {
  margin-bottom: 40px; }

.logo {
  position: relative;
  top: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.logo img {
  width: 400px; }

.text-auth {
  font-size: 20px;
  margin-bottom: 30px;
  color: white; }

.text-parr {
  font-size: 16px;
  width: 200px;
  padding-bottom: 100px;
  line-height: 20px;
  font-style: italic; }

.btns-auth {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%; }

.btn-auth {
  margin-bottom: 30px; }

.task-form {
  margin-bottom: 40px;
  margin-top: 40px; }

.task-new {
  margin-bottom: 20px; }

.sorts {
  margin-bottom: 20px; }

.task-box {
  margin-bottom: 20px;
  font-size: 20px;
  width: 340px; }

.task-box .header {
  height: 20px; }

.task-box h3, .task-box p {
  margin: 10px 0; }

.menu-footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  display: flex;
  justify-content: center; }

.menu-footer li {
  padding: 0 20px; }

.task-form {
  display: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 80%; }

h4 {
  text-align: center;
  font-size: 40px; }

.select-box {
  margin: 0 10px; }

.task-form label {
  margin-left: 35px; }

.form-field {
  display: flex; }

.task-form Button {
  width: 100%;
  margin-top: 20px; }

.form-field i {
  padding-right: 10px;
  margin-bottom: 8px; }

.collapseBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #383bd1;
  color: white;
  padding: 10px 10px 10px 20px;
  border: none;
  width: 100%;
  font-size: 16px; }

.classParentString {
  overflow: hidden;
  width: 100%; }

.openedCollapsible {
  transition: background-color 1.5s ease;
  background-color: rgba(155, 187, 255, 0.8);
  padding: 0 0 20px 0; }

.openedCollapsible:hover {
  background-color: #d0effc; }

.closedCollapsible {
  background-color: rgba(234, 249, 255, 0.8); }

.created {
  margin-bottom: 10px; }

.timer {
  margin-bottom: 20px; }

.news-box, .col {
  display: flex;
  flex-wrap: nowrap; }

.news-article {
  margin: 0 20px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.card-title {
  color: black;
  text-align: left; }

.card p {
  font-size: 14px;
  text-align: left; }

.last-box {
  margin-top: 100px; }

.stats {
  display: flex;
  justify-content: center;
  align-items: center; }

.stats img {
  width: 90%; }

.nav-style ul {
  display: flex;
  list-style: none;
	justify-content: right;
	align-items: center;
	flex-direction: row-reverse;
	;
}

.nav-style li {
  margin-right: 20px;
  color: white;
}

.nav-style a {
  color: white;
  text-decoration: underline !important;
}

.logo2 {
	position: absolute;
	display: inline-block;
	top: 0px;
	right:44%;
}

.logo-nav {
	padding-top: 20px;
	height: 70px;
}
